.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  flex: 0 0 17.5rem;
  height: 100vh;

  & > * {
    width: 100%;
  }


  &__top {
    margin-top: 3rem;
  }

  &__logobox {
    height: 15rem;
    width: 15rem;
    background-color: orangered;
    border-radius: 50%;
    transform: translateX(66%);
    margin-bottom: 2rem;
  }

  &__mid {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: auto;
  }

  &__list {
    list-style: none;
    width: 100%;
    margin-bottom: 1rem;
  }

  &__list-item {
    text-align: center;
  }

  &__link:link,
  &__link:visited {
    width: 100%;
    text-decoration: none;
    text-transform: uppercase;
    display: inline-block;
    padding: 1.2rem 0;
  }

  &__bottom {
    text-align: center;
    margin-bottom: 3rem;
  }

  &__info {
    margin-bottom: 1rem;
  }
}


